.editor { 
  font-family: Times New Roman, monospace ,serif;
  background: white;
  outline: none;
  color: black;
  width: 99%;
  height: 100%;
  padding: 5px;
  /* scroll: auto; */
  align-self: center;
  overflow: auto;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
}

.badge {
  font-family: Helvetica;
  font-weight: bold;
  background-color: transparent;
  color: tomato;
  text-align: center;
  padding-right: 1em; /* used to be 10px */
  padding-top: 0;
  font-size: 0.65em;
  cursor: pointer;
  user-select: none;
  height: 100%;
}

p {
  margin: 0;
}
